// The colours used throughout the site.
$base-colour: #444;
$background-colour: #f5f6f7;

// Primary gradient.
$primary-gradient-start: rgb(1, 96, 182);
$primary-gradient-stop: rgb(71, 131, 133);

// Secondary gradient.
$secondary-gradient-start: #0C8B90;
$secondary-gradient-start: #AB4180;

// The general spacing between items vertically.
$vertical-rhythm: 2vh;

// Typography.
$font-size: 1.8vw;
$font-family: 'Helvetica', sans-serif;
$font-colour: $base-colour;
$font-weight: 300;

html, body {
  height: 100%;
  margin: 0;
  padding: 0;
}

*+* {
  margin-top: $vertical-rhythm;
}
